import { useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";

function City() {
  const { cities } = useSelector((state) => state.cities);
  const params = useParams();
  const city = cities.find((city) => +params.id === city.id);
  return (
    <div className="bg-[#42484D] space-y-5 pl-8 py-6 rounded-md">
      <div>
        <h3 className="text-[#96A4AA] text-xs">CITY NAME</h3>
        <div className="space-x-3">
          <span className="text-3xl font-bold">{city.emoji}</span>{" "}
          <span className=" text-xl font-semibold">{city.cityName}</span>
        </div>
      </div>
      <div>
        <h3 className="text-[#96A4AA] text-xs">
          YOU WENT TO {city.cityName.toUpperCase()} ON
        </h3>
        <p className=" text-xl font-semibold">
          {new Date(city.date).toDateString()}
        </p>
      </div>
      <div>
        {city.notes !== "" && (
          <div>
            <h3 className="text-[#96A4AA] text-xs">YOUR NOTES</h3>
            <p className=" text-xl font-semibold mb-5">{city.notes}</p>
          </div>
        )}
        <h3 className="text-[#96A4AA] text-xs">LEARN MORE</h3>
        <a
          href={`https://en.wikipedia.org/wiki/${city.cityName}`}
          className="block underline text-orange-400"
          target="_blank"
          rel="noreferrer"
        >
          Checkout {city.cityName} on WIKIPEDIA &rarr;
        </a>
      </div>
      <Link
        to="/app/cities"
        className="inline-block text-base px-4 py-2 border-white border rounded-md"
      >
        &larr;BACK
      </Link>
    </div>
  );
}

export default City;
