import { Link, useLoaderData, useNavigate } from "react-router-dom";
import Button from "./Button";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { addCity } from "./CitiesSlice";
import "react-datepicker/dist/react-datepicker.css";
import ReactDatePicker from "react-datepicker";
function CityForm() {
  const data = useLoaderData();
  const [cityName, setCityName] = useState(data.city);
  const [date, setDate] = useState(new Date().toDateString());
  const [notes, setNotes] = useState("");
  const navigate = useNavigate();
  useEffect(
    function () {
      setCityName(data.city);
    },
    [data]
  );
  const dispatch = useDispatch();
  function handleAdd(e) {
    e.preventDefault();
    const newCity = {
      cityName,
      date,
      notes,
      country: data.countryName,
      emoji: data.countryCode,
      position: {
        lat: data.latitude,
        lng: data.longitude,
      },
      id: Math.trunc(Math.random() * 99999999) + 10000000,
    };
    dispatch(addCity(newCity));
    navigate("/app/cities");
  }
  return (
    <form
      className="flex flex-col bg-[#42484D] px-8 py-6 rounded-md"
      onSubmit={(e) => handleAdd(e)}
    >
      <label className="font-semibold">City Name</label>
      <input
        value={cityName}
        onChange={(e) => setCityName(e.target.value)}
        type="text"
        className="bg-[#D6DEE0] mb-8 p-2 rounded text-black focus:bg-white focus:ring-[1px] focus:ring-offset-2 focus:ring-blue focus:ring-offset-[#42484D] focus:outline-none "
      />
      <label className="font-semibold">When did you go to </label>
      <ReactDatePicker
        selected={date}
        onChange={(date) => setDate(date)}
        className="bg-[#D6DEE0] mb-8 p-2 rounded text-black focus:bg-white focus:ring-[1px] focus:ring-offset-2 focus:ring-blue focus:ring-offset-[#42484D] focus:outline-none w-full"
      />
      <label className="font-semibold">Notes about your trip to</label>
      <textarea
        value={notes}
        onChange={(e) => setNotes(e.target.value)}
        className="bg-[#D6DEE0]  rounded text-black  focus:bg-white focus:ring-[1px] focus:ring-offset-2 focus:ring-blue focus:ring-offset-[#42484D] focus:outline-none p-2"
      />
      <div className="flex justify-between mt-6">
        <Button className="font-semibold px-4">ADD</Button>
        <Link
          to="/app/cities"
          className="inline-block text-base px-4 py-2 border-white border rounded-md"
        >
          &larr;BACK
        </Link>
      </div>
    </form>
  );
}

export default CityForm;

export async function loader({ request }) {
  const url = new URL(request.url);
  const searchParams = url.searchParams;
  const lat = searchParams.get("lat");
  const lng = searchParams.get("lng");

  const res = await fetch(
    `https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${lat}&longitude=${lng}`
  );
  const data = await res.json();

  return data;
}
