import { NavLink } from "react-router-dom";

function Logo({ className }) {
  return (
    <NavLink to="/" className={`${className}`}>
      <img
        src={`${process.env.PUBLIC_URL}/logo.png`}
        alt="logo"
        className={`w-[225px] `}
      />
    </NavLink>
  );
}

export default Logo;
