import { NavLink } from "react-router-dom";
import Button from "./Button";
import Logo from "./Logo";

function NavBar() {
  return (
    <nav className="flex justify-between items-center absolute top-0 left-0 w-[100%] px-[50px] py-[25px] z-10 ">
      <Logo />
      <ul className="flex items-center gap-9 text-lg  font-medium">
        <li>
          <NavLink className="pagelink" to="/pricing">
            Pricing
          </NavLink>
        </li>
        <li>
          <NavLink className="pagelink" to="/product">
            Product
          </NavLink>
        </li>
        <li>
          <Button to="login" className=" px-5 py-1 ">
            Login
          </Button>
        </li>
      </ul>
    </nav>
  );
}

export default NavBar;
