function Profile({ onClick }) {
  return (
    <div className="flex items-center gap-4 bg-[#2D3439] absolute top-4 right-4 z-10 px-4 py-2 rounded-md">
      <img
        src="https://i.pravatar.cc/100?u=zz"
        alt="profile"
        className="rounded-full w-10"
      />
      <p>Welcome, Jack</p>
      <button
        onClick={onClick}
        className="text-xs font-bold px-4 py-2 bg-[#42484D] rounded-md"
      >
        LOGOUT
      </button>
    </div>
  );
}

export default Profile;
