import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { deleteCity, selectCity } from "./CitiesSlice";

function CityItem({ city }) {
  const { active } = useSelector((state) => state.cities);

  const dispatch = useDispatch();
  const {
    cityName,
    emoji,
    date,
    id,
    position: { lat, lng },
  } = city;

  function handleDelete(e, id) {
    e.preventDefault();
    dispatch(deleteCity(id));
  }
  return (
    <li className="bg-[#42484D] rounded-md border-l-[6px] border-green-600">
      <NavLink
        onClick={() => dispatch(selectCity(city.id))}
        to={`/app/cities/${id}?lat=${lat}&lng=${lng}`}
        className={` grid rounded-r-md grid-cols-[auto,1fr,auto,auto] items-center py-2 ${
          active === city.id ? "active" : ""
        }`}
      >
        <span className=" inline-block px-5 text-xl font-bold">{emoji}</span>
        <span className="text-xl font-medium">{cityName}</span>
        <span className="font-light">({new Date(date).toDateString()})</span>
        <span className="px-4">
          <button
            onClick={(e) => handleDelete(e, city.id)}
            className="rounded-full  bg-[#2D3439] flex w-5 h-5 justify-center items-center hover:bg-orange-300 duration-[300ms] hover:text-black"
          >
            &times;
          </button>
        </span>
      </NavLink>
    </li>
  );
}

export default CityItem;
