import Main from "../components/Main";

function Pricing() {
  return (
    <Main className="flex items-center justify-center gap-16 pb-28">
      <div className="w-[40%]">
        <h1 className="text-[42px] leading-tight font-medium mb-10">
          Simple pricing. <br />
          Just $9/month.
        </h1>
        <p className="text-md">
          Lorem ipsum dolor, sit amet consectetur adipisicing elit. Vitae vel
          labore mollitia iusto. Recusandae quos provident, laboriosam fugit
          voluptatem iste.
        </p>
      </div>
      <img
        src={`${process.env.PUBLIC_URL}/img-2.jpg`}
        alt="pricing"
        className=" w-[38%]"
      />
    </Main>
  );
}

export default Pricing;
