import { useDispatch, useSelector } from "react-redux";

import CityItem from "./CityItem";
import { useEffect } from "react";
import { updateCities } from "./CitiesSlice";

function Cities() {
  const { cities } = useSelector((state) => state.cities);
  const dispatch = useDispatch();
  useEffect(() => {
    const citiesJson = localStorage.getItem("cities");
    const storedCities = citiesJson ? JSON.parse(citiesJson) : null;
    if (storedCities !== null) {
      dispatch(updateCities(storedCities));
    }
  }, [dispatch]);
  useEffect(
    function () {
      localStorage.setItem("cities", JSON.stringify(cities));
    },
    [cities]
  );

  if (!cities.length)
    return (
      <p className="text-xl font-bold text-center mt-4">
        Start by adding some locations
      </p>
    );
  return (
    <ul className="flex flex-col gap-4 cities">
      {cities.map((city) => (
        <CityItem city={city} key={city.id} />
      ))}
    </ul>
  );
}

export default Cities;
