function CountryItem({ country }) {
  return (
    <li className="  bg-[#42484D] rounded-md border-l-[6px] border-[#FFB545] flex flex-col  w-[48%]  text-center py-2">
      <span className="text-3xl font-extrabold">{country.emoji}</span>
      <span className="text-lg font-semibold">{country.country}</span>
    </li>
  );
}

export default CountryItem;
