import {
  MapContainer,
  Marker,
  Popup,
  TileLayer,
  useMap,
  useMapEvents,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { useNavigate, useNavigation, useSearchParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { logout, setCurrentPosition } from "./CitiesSlice";
import Button from "./Button";
import { getLocation } from "./getLocation";
import Profile from "./Profile";

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png",
  iconUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png",
  shadowUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png",
});

function Map() {
  const { cities } = useSelector((state) => state.cities);
  const loadingState = useNavigation();
  const isLoading = loadingState.state !== "idle";
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [position, setPosition] = useState([0, 40]);
  const lat = +searchParams.get("lat");

  const lng = +searchParams.get("lng");

  useEffect(
    function () {
      if (lat && lng) setPosition([lat, lng]);
    },
    [lat, lng]
  );
  async function handleGetPosition() {
    const data = await getLocation();
    const lat = data.coords.latitude;
    const lng = data.coords.longitude;
    navigate(`/app/form?lat=${lat}&lng=${lng}`);
  }
  const dispatch = useDispatch();
  function handleLogout() {
    dispatch(logout());
  }
  return (
    <div className="w-full h-full relative">
      <Button
        disabled={isLoading}
        onClick={handleGetPosition}
        className="absolute left-[50%] translate-x-[-50%] bottom-10 z-10 px-4 py-2 text-sm font-medium disabled:bg-gray-500"
      >
        {isLoading ? "loading..." : "USE YOUR POSITION"}
      </Button>
      <Profile onClick={handleLogout} />
      <div className="w-full h-full relative z-0">
        <MapContainer
          center={position}
          zoom={13}
          scrollWheelZoom={false}
          className="w-full h-full "
        >
          <ChangePosition position={position} />
          <HandleClick />
          <TileLayer
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <Marker position={position}></Marker>
          {cities.map((el) => (
            <Marker position={[el.position.lat, el.position.lng]} key={el.id}>
              <Popup>
                {el.emoji} {el.cityName} <br /> {el.notes}
              </Popup>
            </Marker>
          ))}
        </MapContainer>
      </div>
    </div>
  );
}

export default Map;

function ChangePosition({ position }) {
  const map = useMap();
  map.setView(position);
  return null;
}
function HandleClick() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useMapEvents({
    click: (e) => {
      navigate(`/app/form?lat=${e.latlng.lat}&lng=${e.latlng.lng}`);
      dispatch(setCurrentPosition(`${e.latlng.lat}+${e.latlng.lng}`));
    },
  });
  return null;
}
