import Button from "../components/Button";
import Main from "../components/Main";

function Home() {
  return (
    <Main className="homepage bg-center pt-40 pb-20">
      <div className="absolute inset-0 bg-black opacity-70 z-0"></div>
      <div className=" text-center z-1 relative pt-16">
        <h1 className=" text-5xl font-semibold leading-tight mb-8">
          You travel the world. <br /> WorldWise keeps track of your adventures.
        </h1>
        <p className=" text-xl mb-16">
          A world map that tracks your footsteps into every city you can think
          of. Never forget your wonderful experiences, and show your friends how
          you have wandered the world.
        </p>
        <Button to="/app" className=" px-8 py-2  text-xl">
          Start Tracking Now
        </Button>
      </div>
    </Main>
  );
}

export default Home;
