const { NavLink } = require("react-router-dom");

function Button({ children, className, to, onClick, disabled }) {
  const style = ` bg-green-500 text-[#242a2e] inline-block rounded-md ${className}`;
  if (to)
    return (
      <NavLink className={style} to={to}>
        {children}
      </NavLink>
    );
  return (
    <button disabled={disabled} onClick={onClick} className={style}>
      {children}
    </button>
  );
}

export default Button;
