import { NavLink, Outlet, useNavigate } from "react-router-dom";
import Logo from "./Logo";
import Map from "./Map";
import { useSelector } from "react-redux";
import { useEffect } from "react";

function AppLayout() {
  const navigate = useNavigate();
  const { currentPosition, loggedin } = useSelector((state) => state.cities);
  useEffect(
    function () {
      if (!loggedin) navigate("/login");
    },
    [loggedin, navigate]
  );
  return (
    <div className="h-[92dvh] flex ">
      <div className="max-w-[550px] bg-[#2D3439] h-[100%] flex flex-col items-center w-[100%]">
        <Logo className=" my-8" />
        <ul className="flex w-fit bg-[#42484D] rounded-md tabbed">
          <li>
            <NavLink
              to="/app/cities"
              className="inline-block px-4 py-[3px] rounded-md"
            >
              Cities
            </NavLink>
          </li>
          <li>
            <NavLink
              to="countries"
              className="inline-block px-4 py-[3px] rounded-md"
            >
              Countries
            </NavLink>
          </li>
        </ul>
        <main className="w-[82%] mt-5 overflow-y-scroll" key={currentPosition}>
          <Outlet />
        </main>
        <footer className="mt-auto text-xs font-light mb-8">
          © Copyright 2024 by WorldWise Inc.
        </footer>
      </div>
      <Map />
    </div>
  );
}

export default AppLayout;
